.App {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card{
  background-color: white;
  width: 700px;
  height: 500px;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  padding: 40px 50px;
  border-radius: 20px;
  border-bottom-right-radius: 30%;
}

.top{
  display: flex;
  gap: 20px;
}

.top div{
  display: flex;
  flex-direction: column;
}

.top div label{
  font-weight: 700;
  color: hsl(0, 1%, 44%);
  text-transform: uppercase;
  letter-spacing: 2px;
}

.top div input{
  width: 130px;
  height: 50px;
  padding: 10px;
  border-radius: 5px;
  border: 1px hsl(0, 1%, 44%) solid;
  outline: none;
  font-size: 22px;
  font-weight: 700;
}

.submit{
  width: 60px;
  height: 60px;
  background-color: hsl(259, 100%, 65%);
  border: none;
  outline: none;
  border-radius: 50%;
  align-self: flex-end;
  position: relative;
  cursor: pointer;
}

.submit:hover{
  background-color: hsl(0, 0%, 8%);
}

.submit img{
  width: 30px;

}

.submit::before{
  content: "";
  background-color: hsl(0, 1%, 44%);
  width: 540px;
  height: .5px;
  position: absolute;
  left: -540px;
  top: 50%;
}

.bottom{
  display: flex;
  flex-direction: column;
}

.bottom h1{
  font-size: 4.5em;
  font-weight: 800;
  font-style: italic;
  margin-bottom: -20px;
}

.bottom h1 span{
  color: hsl(259, 100%, 65%);
}

.err{
  color: hsl(0, 100%, 67%);
  font-size: 12px;
  font-style: italic;
}